<template>
  <div class="col-lg-8 col-md-10 mx-auto text-center myClass">
    <h2 class="section-heading mx-auto">{{ data.title }}</h2>
    <br /><br />
    <!-- 
     <a href="#">
      <img class="img-fluid" :src="'/img/'+data.imageUrl" alt="" /> <br><br>
    </a> -->

    <h4>
      {{ data.description }}
    </h4>
    <br /><br />

    <div v-show="data.commits.length > 0" class="container">
      <div
        v-for="commit in data.commits"
        :key="commit.index"
        class="card text-center mb-3"
        v-show="commit.description!=null"
      >
        <div class="card-body">
          <h5 class="card-title">{{ commit.description }}</h5>
          <p class="card-text"></p>
        </div>
        <div class="card-footer text-muted">
          {{ new Date(commit.updatedAt) }}
          <h4 class="text-muted">{{ commit.user.name }}</h4>
        </div>
      </div>
    </div>

    <blockquote class="blockquote">
      Lütfen Değerli Bilgilerinizi Giriniz Bilgi Paylaştıkça Çoğalır !
    </blockquote>
    <div class="row mb-1">
      <textarea name="commit" cols="90" rows="5" v-model="commit"></textarea>
    </div>
    <div class="row justify-content-end">
      <button @click.prevent="addCommitt" class="btn btn-info">
        Yorum Ekle
      </button>
    </div>
  </div>
</template>

<script>
//import PostService from "../services/PostAPI";
import { mapActions, mapGetters } from "vuex";
export default {
  data(){
    return{
      commit:""
    }
  },
  methods: {
    ...mapActions(["getByName","addCommit"]),
    ...mapGetters(["getOnePost"]),
    addCommitt(){
      if(this.commit!=""){
        //postId Gidecek ve commit gidecek
        let commitInstance={
          postId:this.data.id,
          commit:this.commit,
        }
        this.addCommit(commitInstance);
      }else{
        this.$alert("Lütfen Bişeyler Giriniz !","Uyarı","info")
      }
    }
  },
  computed: {
    data() {
      return this.getOnePost();
    },
  },
  created() {
    this.getByName(this.$route.params.id);
    window.scrollTo(0, 0);
  },
};
</script>


<style scoped>
.myClass {
  padding-top: 3%;
  padding-bottom: 3%;
}
p {
  text-align: center;
}
</style>